            left: 600px;
            
            opacity: 95%;
            height: 60px;
        }

        #icons{
            position: absolute;
            left: 10px;
            top: 10px;

        }
        
        @media (max-width:1400px){
            #aprendizado{
                position: absolute;
                left: -65px;
            }
            #conteudo{
                width: 600px;
            }
        }
        @media(max-width:1200px){
            #conteudo{
                font-size:14px;
                left: -20px;
                top: 250px;
                width: 500px;
            }
            #iconBootstrap{
                position: absolute;
                left: 10px;
            }
            #iconUml{
                position: absolute;
                top: 180px;
                left: 5px;
            }
            #iconJquery{
                position: absolute;
                width: 40px;
                left: 10px;
                top: 100px;
            }

            
  
    }
    @media(max-width:1250px){
        #aprendizado{

        left: -100px;
        }
    }
    

        @media(max-width:800px){
            #aprendizado{
                position: absolute;
                left: -100px;
                width: 50px;
            }
            #conteudo{
                width: 350px;
                left: 10px;
            }
            #iconGulp{
                top: 175px;
                left: 200px;
            }
            #iconMysql{
           top: 175px;
           left: 300px;
            }
           #iconReact,#iconHtml,#iconCss,#iconJs,#iconGulp,#iconBootstrap,#iconJquery,#iconWebPack,#iconNpm,#iconNode
           ,#iconJava,#iconMysql{
            width: 50px;
           }
           #iconUml{
            width: 100px;
            left: 20px;
           }
        }

      